export const Chords: Record<string, IChord> = Object.keys(ChordsList).reduce<any>(
  (chords, note) => {
    ChordsList[note.charAt(0)].forEach((elem) => {
      chords[elem.chord] = elem;
    });
    return chords;
  },
  {}
);

export const ChordsKeys = Object.keys(Chords);
